'use strict';

const e = React.createElement;
const BenefitInclude = () => (
  <div className="icon icon icon__pricing-include">
    <figure>
    <img src={require('/assets/icons/check.svg')} className="icon--invert" alt="Exclude" />
    </figure>
  </div>
)

const BenefitExclude = () => (
  <div className="icon icon__pricing-exclude">
    <figure>
    <img src={require('/assets/icons/x.svg')} className="icon--invert" alt="Exclude" />
    </figure>
  </div>
)

const ConditionedIcon = (props) => {
  const { state } = props;
  
  if(state === true) return <BenefitInclude/>
  return <BenefitExclude/>
}

